import React from 'react';
import { Link } from "react-router-dom";
import "./Home.css";

export default function Home(props) {
    return (
        <div className="Home-page">
            
           
            <button className={'greeting-button'}> <img src="../img/flower_daisy.png" width="16px" height="16px" alt=""/><Link to={'/testing'} >Начать проходить тесты!</Link></button>
            <button className={'greeting-button'}><i className={'iconlol'}></i><Link to={'/results'} >Посмотреть свои результаты</Link></button>
        </div>
    );
}